.front-panel {
	//border: 1px solid fuchsia;
	height: auto;
	width: 80%;
	text-align: center;
}

.button-wrapper {
	//border: 1px solid fuchsia;
	display: flex;
	flex-direction: row; //Needed to make element full width  <---
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.generator-button__box {
	//flex-basis: 100%; //Needed to make element full width <---
	display: flex;
	flex-direction: row; //Needed to make element full width  <---
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	img {
    margin-top: 10px;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
    width: 90%;
    transition: transform 0.05s ease-out;
  }
  img:hover {
  }
	img:active {
		transform: translateY(2px);
	}
}

.settings-button {
  //border: 1px solid red;
  position: absolute;
  top: 5px;
  right: 10px;
  img {
    width: 30px;
    transition: transform 0.5s ease-in-out;
  }
  img:hover {
    transform: scale(1.1);
		//transform: rotate(-36deg); //FOR NON TOUCH SCREENS
  }
}



