.ReactModal__Overlay {
  //border: 1px solid hotpink;
  background-color: transparent !important;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.ReactModal__Content {
  border-top: 20px solid rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(15px);
  border-radius: 7px;
  width: 95%;
  height: 90%;
  color: $off-white;
  outline: none;
}

.modal-wrapper {
  margin-left: auto;
  margin-right: auto;
  padding: 0 7px 7px 7px;
  width: 90%;
  border: 1px solid hotpink;
  div {
    //border: 1px solid hotpink;
    margin-top: 10px;
  }
  input {
    margin: 5px 5px 0 0;
  }
}

.team-size_inputs {
  display: inline-block;
  width: 100%;
}

.team-size_dropdown {
  display: inline-block;
  text-align-last:center;
  width: 25%;
  padding: 3px;
  border-radius: 5px;
    background: rgba(5, 0, 5, 0.72);    
    border: none;
    color: white;
    font-family: Bourgeois;
    font-size: 1.2rem;
}

label[for="teams"] {
  margin-left: 10px;
  font-size: 1.1rem;
}

.shuffle_input {
  display: inline-block;
  width: 45%;
}

.player-name_inputs {
  display: flex;
  flex-wrap: wrap;
  input[type=text] {
    padding: 4px;
    width: 45%;
    border-radius: 5px;
    background: rgba(5, 0, 5, 0.72);    
    border: none;
    color: white;
    font-family: Bourgeois;
    font-size: 1.2rem;
    text-transform: uppercase;
  }
  input[type=text]:disabled {
    background: rgba(5, 0, 5, 0.12);
  }
}

.randomness-sliders {
  p {
    margin-bottom: -3px;
    display:block;
  }

  .random-value {    
  }
  
  input[type=range] {
    width: 95%;
  }
}

.switches {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  div {
    width: 45%;
    margin-right: 15px;
  }
}

.switch-label {
  margin-left: 5px;
}

.odometer-min-max-inputs {
  display: flex;
  flex-wrap: wrap;
  p {
    display: block;
    width: 100%;
    margin-bottom: -3px;
  }
  input[type=number] {
    padding: 4px;
    width: 45%;
    border-radius: 5px;
    background: rgba(5, 0, 5, 0.72);    
    border: none;
    color: white;
    font-family: Bourgeois;
    font-size: 1.2rem;
    text-transform: uppercase;
  }
}

.settings-buttons {
  border-radius: 5px;
}
