.scene {
  //border: 1px solid fuchsia;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
  perspective: -10%;  
}

.card {
  //border: 1px solid red;
  border-radius: 8px;
  height: 95%;
  width: 95%;
  position: relative;
  transition: ease-in-out 1.35s;
  transform-style: preserve-3d;
  transform: translateZ(-100px);
}

.card__face {  
  position: absolute;
  height: 100%;
  width: 100%;
  backface-visibility: hidden;
}

.card__face--front {
  //border: 1px solid blue;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.card__face--back {
  //border: 1px solid hotpink;
  backdrop-filter: blur(15px);
  border-radius: 7px;
  color: $off-white;
  transform: rotateY(-180deg);
}

.card.is-flipped {
  transform: rotateY(-180deg);
}

.card.is-flipped .card__face--front {
  transition-delay: 0.65s;
  visibility: hidden;
}